.title {
  width: 100vw;
  font-weight: bold;
  letter-spacing: 2px;
  font-size: 60px;
  text-align: center;
  position: fixed;
  top: 20vh;
  user-select: none;
}
.searchFrame {
  width: fit-content;
  margin: 40vh auto 0;
  .searchBar {
    display: flex;
    align-items: center;
    font-size: 18px;
    box-sizing: border-box;

    .input {
      outline: none;
      border: none;
      border-bottom: #bbbbbb 2px solid;
      color: #eeeeee;
      background-color: #222222;
      font-size: 18px;
      font-family: "Segoe UI", sans-serif;
      box-sizing: border-box;
      width: 400px;
      height: 36px;
      padding: 0 8px;

      &:hover {
        border-bottom: #eeeeee 2px solid;
      }
      &::placeholder {
        user-select: none;
      }
    }
    .searchIcon {
      width: 20px;
      height: 20px;
      margin-left: 8px;
      &:hover {
        color: gold;
        cursor: pointer;
      }
    }
  }
  .promptFrame {
    user-select: none;
    .prompt {
      width: 400px;
      background-color: #282828;
      box-sizing: border-box;
      padding: 2px 8px;
      font-size: 18px;
      &:hover {
        cursor: pointer;
        background-color: #bbbbbb;
        color: #222222;
      }
    }
  }
}
