.wiki-content {
  box-sizing: border-box;
  padding: 40px;
  color: #bbbbbb;
  background-color: #333333;
  width: 70%;
  margin: 0 auto;
  font-size: 18px;
  overflow-x: hidden;

  .wiki-title {
    font-size: 40px;
    line-height: 40px;
    padding-bottom: 20px;
    border-bottom: #bbbbbb solid 2px;
    margin-bottom: 20px;
    font-weight: bold;
  }

  .wiki-main {
    .side-box,
    .spoken-wikipedia,
    .navbox,
    .ambox,
    .portalbox,
    .mw-editsection,
    audio,
    .sidebar,
    .sister-bar,
    .plainlinks,
    .cs1-code,
    .cs1-visible-error,
    .portal-bar {
      display: none !important;
    }

    .thumb {
      width: fit-content;
      margin: 20px;
      &.tleft {
        float: left;
        clear: left;
      }
      &.tright {
        float: right;
        clear: right;
      }
    }

    #coordinates .plainlinks {
      display: inline !important;
    }

    .infobox {
      margin: 0 auto;
    }

    .biota {
      width: 100% !important;
      max-width: 600px !important;
    }

    .ib-country {
      width: 100% !important;
      max-width: 800px !important;
    }

    table:not(.clade) {
      border: #bbbbbb solid 1px !important;
      border-collapse: collapse;

      tr {
        th {
          background-color: #444444 !important;
          border: #bbbbbb solid 1px !important;
        }

        td {
          background-color: #333333 !important;
          border: #bbbbbb solid 1px !important;

          &.anthem {
            text-align: center;
          }
        }
      }
    }
    table.clade {
      font-size: 10px;
      p {
        margin: 0;
      }
    }

    .infobox .infobox-header,
    .infobox .infobox-subheader,
    .infobox .infobox-image,
    .infobox .infobox-full-data,
    .infobox .infobox-below {
      text-align: center;
    }

    .infobox-label,
    .infobox-data,
    .infobox th,
    .infobox td {
      text-align: left;
    }

    .infobox .infobox-above,
    .infobox .infobox-title,
    .infobox caption {
      font-weight: bold;
      text-align: center;
    }

    .infobox-full-data {
      div {
        background-color: #333333 !important;
      }
    }

    .noresize {
      div {
        display: none;
      }
    }

    a {
      color: #eeeeee;
      text-decoration: none;

      &:hover {
        color: gold;
      }
    }

    p {
      margin: 20px 0;
    }
  }
}
